import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useNavigate } from "react-router-dom";
import { Fragment, useState } from "react";
import CreateButton from "../../../Ui/Button/CreateButton";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import FormInput from "../../../Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import { useFeedbackCreateStudentFeedback } from "../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import QvipleLoader from "../../../Loader/QvipleLoader";

const StudentCreateFeedback = ({ instituteId }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [state, setState] = useState({
    feedback_name: "",
    feedback_type: "Range",
    questions: [addFeedbackQuestion()],
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [feedbackCreateStudentFeedback] = useFeedbackCreateStudentFeedback();

  const onClose = () => {
    navigate(-1);
  };

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const addQuestion = () => {
    setState((prev) => ({
      ...prev,
      questions: [
        ...prev.questions,
        addFeedbackQuestion(prev.questions?.length + 1),
      ],
    }));
  };

  const addQuestionOption = (index) => {
    let questions = state.questions;
    if (questions[index]?.options?.length < 5) {
      questions[index]?.options?.push(
        addFeedbackQuestionOption(questions[index]?.options?.length + 1)
      );
      setState((prev) => ({
        ...prev,
        questions: questions,
      }));
    }
  };
  const removeQuestionOption = (index, optIndex) => {
    let questions = state.questions;
    questions[index].options = questions[index].options?.filter(
      (ot) => ot?.option_sno !== `${optIndex + 1}`
    );
    let count = 1;
    for (let o of questions[index].options) {
      o.option_sno = `${count}`;
      ++count;
    }
    setState((prev) => ({
      ...prev,
      questions: questions,
    }));
  };

  const onQuestionChange = (e, index) => {
    const inputarr = [...state.questions];
    for (let inp = 0; inp < inputarr?.length; inp++) {
      if (inp === index) {
        inputarr[inp].question_title = e.target.value;
      }
    }
    setState((prev) => ({
      ...prev,
      questions: inputarr,
    }));
  };
  const onQuestionOptionChange = (e, index, optIndex) => {
    const inputarr = [...state.questions];
    inputarr[index].options[optIndex].option = e.target.value;
    setState((prev) => ({
      ...prev,
      questions: inputarr,
    }));
  };

  const onCreate = () => {
    let validation = requiredField(
      {
        feedback_name: state.feedback_name,
      },
      []
    );
    if (existingRequiredField(validation)) {
      if (instituteId) {
        feedbackCreateStudentFeedback({
          id: instituteId,
          studentFeedback: state,
        })
          .then(() => {
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch(() => {
            setDisabled((pre) => !pre);
          });
      }
    } else {
      setErrorField(validation);
    }
  };
  // console.info("sdfhjsdfs", state);
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("student_feedback")}</h6>
          <img
            src={`${assestsUserFinanceUrl}/close.svg`}
            alt="close icon"
            onClick={onClose}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
        <FormInput
          label={t("feedback_name")}
          placeholder={t("feedback_name_placeholder")}
          name={"feedback_name"}
          value={state?.feedback_name}
          type={"text"}
          onChange={onChange}
          errorShow={errorField["feedback_name"]}
        />

        {state.questions?.map((que, index) => (
          <Fragment key={`question${que?.question_sno}`}>
            <FormTextarea
              label={`${t("question")} :  ${que?.question_sno}`}
              placeholder={t("question_placeholder")}
              name={"question"}
              value={que?.question_title}
              type={"text"}
              onChange={(e) => onQuestionChange(e, index)}
              // errorShow={errorField["incomeDesc"]}
            />
            {que?.options?.map((opt, optIndex) => (
              <FormInput
                key={`option${optIndex}`}
                label={`${t("option")} : ${opt?.option_sno}`}
                placeholder={t("option_placeholder")}
                name={`option${optIndex}`}
                value={opt?.option}
                type={"text"}
                onChange={(e) => onQuestionOptionChange(e, index, optIndex)}
                errorShow={errorField[opt[optIndex]]}
                customFormInputLabel={{
                  display: "flex",
                  alignItmes: "center",
                  justifyContent: "space-between",
                }}
              >
                {optIndex < 2 ? null : (
                  <CreateButton
                    label={t("remove")}
                    customStyle={{
                      margin: "0 0 0 0",
                      padding: "0.1rem 0.5rem",
                      width: "fit-content",
                      fontSize: "12px",
                      fontWeight: "400",
                    }}
                    onAction={() => removeQuestionOption(index, optIndex)}
                  />
                )}
              </FormInput>
            ))}
            {que?.options?.length > 4 ? null : (
              <CreateButton
                label={t("add_option")}
                customStyle={{
                  margin: "0",
                  marginTop: "1rem",
                  padding: "0.2rem 1rem",
                  width: "fit-content",
                }}
                onAction={() => addQuestionOption(index)}
              />
            )}
          </Fragment>
        ))}

        <CreateButton
          label={t("feedback_add_question")}
          customStyle={{
            margin: "0",
            marginTop: "1rem",
            padding: "0.2rem 1rem",
            width: "fit-content",
          }}
          onAction={addQuestion}
        />

        <CreateButton
          label={t("create_feedback")}
          customStyle={{
            padding: "0.4rem 1rem",
          }}
          onAction={onCreate}
        />
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </UserMemberContainerWrapper>
  );
};

export default StudentCreateFeedback;

const addFeedbackQuestion = (args) => {
  return {
    question_sno: args ? `${args}` : "1",
    question_title: "",
    options: [
      {
        option_sno: "1",
        option: "",
      },
      {
        option_sno: "2",
        option: "",
      },
    ],
  };
};

const addFeedbackQuestionOption = (args) => {
  return {
    option_sno: `${args}`,
    option: "",
  };
};
